// Import the bold font
@font-face {
  font-family: 'TheBoldFont';
  src: url('../assets/fonts/THEBOLDFONT-FREEVERSION.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

// Import custom styles
@import '../styles/amount-input.scss';

body {
  background: linear-gradient(135deg, #0f0f1e 0%, #1a1a2e 100%);
  background-attachment: fixed;
  color: #ffffff;
  min-height: 100vh; /* Ensures the background covers the full viewport height */
  position: relative;
  overflow-x: hidden;
  
  /* Background grid effect */
  &::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: 
      linear-gradient(rgba(54, 202, 186, 0.1) 1px, transparent 1px),
      linear-gradient(90deg, rgba(54, 202, 186, 0.1) 1px, transparent 1px);
    background-size: 30px 30px;
    background-position: center center;
    z-index: -2;
    opacity: 0.3;
    pointer-events: none;
  }
  
  /* Glowing orbs in background */
  &::after {
    content: '';
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: 
      radial-gradient(circle at 20% 30%, rgba(54, 202, 186, 0.2) 0%, transparent 25%),
      radial-gradient(circle at 80% 70%, rgba(111, 54, 202, 0.2) 0%, transparent 25%);
    z-index: -1;
    pointer-events: none;
  }
  
  /* Add futuristic glow effect to the background */
  .glow-overlay {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 50% 50%, rgba(54, 202, 186, 0.15) 0%, rgba(10, 10, 26, 0) 70%);
    pointer-events: none;
    z-index: 0;
    animation: pulseBackground 8s infinite alternate ease-in-out;
  }
  
  /* Glowing dots for futuristic effect */
  .glowing-dots {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 0;
    overflow: hidden;
  }
  
  .dot {
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: rgba(54, 202, 186, 0.7);
    box-shadow: 0 0 8px 2px rgba(54, 202, 186, 0.5);
    opacity: 0;
    animation: dotPulse 4s infinite ease-in-out;
    
    &:nth-child(1) { top: 10%; left: 20%; animation-delay: 0s; }
    &:nth-child(2) { top: 15%; left: 80%; animation-delay: 0.5s; }
    &:nth-child(3) { top: 25%; left: 40%; animation-delay: 1s; }
    &:nth-child(4) { top: 30%; left: 65%; animation-delay: 1.5s; }
    &:nth-child(5) { top: 45%; left: 15%; animation-delay: 2s; }
    &:nth-child(6) { top: 55%; left: 75%; animation-delay: 2.5s; }
    &:nth-child(7) { top: 70%; left: 35%; animation-delay: 3s; }
    &:nth-child(8) { top: 85%; left: 60%; animation-delay: 3.5s; }
  }
  
  /* Energy lines for futuristic effect */
  .energy-lines {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 0;
    overflow: hidden;
    
    .line {
      position: absolute;
      background: linear-gradient(90deg, rgba(54, 202, 186, 0) 0%, rgba(54, 202, 186, 0.8) 50%, rgba(54, 202, 186, 0) 100%);
      height: 1px;
      width: 100%;
      opacity: 0;
      transform-origin: left center;
      animation: energyLineScan 8s infinite ease-out;
      
      &:nth-child(1) {
        top: 15%;
        animation-delay: 0s;
      }
      
      &:nth-child(2) {
        top: 35%;
        animation-delay: 2s;
      }
      
      &:nth-child(3) {
        top: 65%;
        animation-delay: 4s;
      }
      
      &:nth-child(4) {
        top: 85%;
        animation-delay: 6s;
      }
    }
  }
  
  /* Floating coins background */
  .floating-coins-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 0;
    overflow: hidden;
  }
  
  .floating-coin {
    position: absolute;
    width: 60px;
    height: 60px;
    opacity: 0.3;
    filter: drop-shadow(0 0 8px rgba(255, 215, 0, 0.3));
    animation: float 15s infinite ease-in-out;
    
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
    
    // Strategic positioning for coins - Desktop
    @media (min-width: 769px) {
      &.coin-1 { top: 15vh; left: 15vw; }
      &.coin-2 { top: 15vh; left: 50vw; }
      &.coin-3 { top: 15vh; left: 85vw; }
      &.coin-4 { top: 40vh; left: 25vw; }
      &.coin-5 { top: 40vh; left: 75vw; }
      &.coin-6 { top: 65vh; left: 15vw; }
      &.coin-7 { top: 65vh; left: 50vw; }
      &.coin-8 { top: 65vh; left: 85vw; }
      &.coin-9 { top: 85vh; left: 30vw; }
      &.coin-10 { top: 85vh; left: 70vw; }
      &.coin-11 { top: 30vh; left: 50vw; }
    }
    
    // Mobile positioning
    @media (max-width: 768px) {
      &.coin-1 { top: 8vh; left: 12vw; }
      &.coin-2 { top: 22vh; left: 76vw; }
      &.coin-3 { top: 35vh; left: 28vw; }
      &.coin-4 { top: 48vh; left: 82vw; }
      &.coin-5 { top: 62vh; left: 18vw; }
    }
    
    // Coin sizes and rotations
    &.coin-1 { transform: scale(1.8) rotate(45deg); }
    &.coin-2 { transform: scale(1.2) rotate(90deg); }
    &.coin-3 { transform: scale(0.7) rotate(135deg); }
    &.coin-4 { transform: scale(1.5) rotate(180deg); }
    &.coin-5 { transform: scale(0.8) rotate(225deg); }
    &.coin-6 { transform: scale(1.3) rotate(270deg); }
    &.coin-7 { transform: scale(0.6) rotate(315deg); }
    &.coin-8 { transform: scale(1.7) rotate(0deg); }
    &.coin-9 { transform: scale(1.0) rotate(45deg); }
    &.coin-10 { transform: scale(0.9) rotate(90deg); }
    &.coin-11 { transform: scale(2.0) rotate(135deg); }
    
    // Random animation properties
    @for $i from 1 through 11 {
      &.coin-#{$i} {
        animation-duration: #{7 + random(6)}s;
        animation-delay: #{random(4)}s;
      }
    }
  }
  
  @keyframes float {
    0% {
      transform: translateY(0) translateX(0) rotate(0deg);
    }
    20% {
      transform: translateY(-25px) translateX(15px) rotate(10deg);
    }
    40% {
      transform: translateY(10px) translateX(30px) rotate(20deg);
    }
    60% {
      transform: translateY(20px) translateX(10px) rotate(30deg);
    }
    80% {
      transform: translateY(-10px) translateX(-20px) rotate(15deg);
    }
    100% {
      transform: translateY(0) translateX(0) rotate(0deg);
    }
  }
  
}

/* Normal cursor is used */

/* Chest Animation Styles */
.chest-animation-container {
  margin-bottom: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  
  /* Control panel at the top */
  .control-panel {
    width: 100%;
    max-width: 600px;
    margin-bottom: 30px;
    z-index: 10;
    perspective: 1000px;
    
    .panel-background {
      position: relative;
      background: rgba(15, 20, 40, 0.85);
      border-radius: 20px;
      padding: 25px;
      box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto auto;
      grid-gap: 20px;
      transform-style: preserve-3d;
      transform: rotateX(5deg);
      overflow: visible; /* Changed from hidden to allow dropdowns to extend beyond */
      
      .opened-chest-container {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        
        .chest-image {
          max-width: 100%;
          height: auto;
          z-index: 1;
        }
        
        .chest-light-animation {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          z-index: 2;
          
          .light-particles {
            position: absolute;
            top: 40%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 60%;
            height: 60%;
            background: radial-gradient(circle, rgba(54, 202, 186, 0.8) 0%, rgba(54, 202, 186, 0) 70%);
            filter: blur(15px);
            animation: pulseLight 1s infinite alternate;
          }
          
          .light-glow {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: radial-gradient(circle at center, rgba(54, 202, 186, 0.3) 0%, rgba(54, 202, 186, 0) 70%);
            filter: blur(20px);
            animation: expandGlow 2s infinite alternate;
          }
        }
        
        /* NFT reveal animation */
        .nft-reveal {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, 0%);
          display: flex;
          flex-direction: column;
          align-items: center;
          z-index: 3;
          opacity: 0;
          animation: nftRiseUp 1.5s forwards;
          
          .nft-image {
            width: 180px;
            height: 180px;
            object-fit: contain;
            filter: drop-shadow(0 0 15px rgba(54, 202, 186, 0.8));
            transform: scale(0.8);
            transition: transform 0.5s ease-out;
          }
          
          .nft-title {
            margin-top: 15px;
            font-family: 'TheBoldFont', sans-serif;
            color: white;
            font-size: 1.2rem;
            text-align: center;
            background: linear-gradient(135deg, #36caba, #6f36ca);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            text-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
            opacity: 0;
            animation: fadeIn 0.5s forwards 0.5s;
          }
          
          &.complete {
            .nft-image {
              transform: scale(1);
            }
          }
        }
      }
      
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background: linear-gradient(90deg, transparent, rgba(54, 202, 186, 0.8), transparent);
        z-index: 2;
      }
      
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background: linear-gradient(90deg, transparent, rgba(54, 202, 186, 0.8), transparent);
        z-index: 2;
      }
      
      .panel-glow {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(circle at center, rgba(54, 202, 186, 0.15) 0%, transparent 70%);
        pointer-events: none;
        z-index: 1;
      }
    }
    
    /* Balance Display with Orb */
    .balance-section {
      grid-column: 1;
      grid-row: 1;
      display: flex;
      align-items: center;
      position: relative;
      z-index: 3;
      
      .balance-orb {
        position: relative;
        width: 80px;
        height: 80px;
        margin-right: 15px;
        
        .orb-inner {
          position: absolute;
          top: 5px;
          left: 5px;
          width: calc(100% - 10px);
          height: calc(100% - 10px);
          background: radial-gradient(circle at 30% 30%, #36caba, #1a5e54);
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          box-shadow: 0 0 20px rgba(54, 202, 186, 0.5), inset 0 0 15px rgba(255, 255, 255, 0.3);
          animation: orbPulse 3s infinite alternate;
          
          .orb-value {
            font-family: "TheBoldFont", sans-serif;
            font-size: 1.8rem;
            color: white;
            text-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
          }
        }
        
        .orb-ring {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border: 2px solid rgba(54, 202, 186, 0.8);
          border-radius: 50%;
          box-shadow: 0 0 15px rgba(54, 202, 186, 0.5);
          animation: ringRotate 10s linear infinite;
          
          &::before {
            content: '';
            position: absolute;
            top: -2px;
            left: 50%;
            width: 10px;
            height: 10px;
            background: #36caba;
            border-radius: 50%;
            transform: translateX(-50%);
            box-shadow: 0 0 10px rgba(54, 202, 186, 0.8);
          }
        }
      }
      
      .balance-info {
        .balance-label {
          font-family: "TheBoldFont", sans-serif;
          font-size: 1.2rem;
          color: #36caba;
          margin: 0 0 5px;
          letter-spacing: 1px;
        }
        
        .balance-subtitle {
          font-size: 0.8rem;
          color: rgba(255, 255, 255, 0.7);
          margin: 0;
        }
      }
    }
    
    /* Amount Selection */
    .amount-section {
      grid-column: 2;
      grid-row: 1;
      position: relative;
      z-index: 100; /* Increased z-index to be above container */
      
      .amount-label {
        font-family: "TheBoldFont", sans-serif;
        font-size: 1.2rem;
        color: #36caba;
        margin: 0 0 10px;
        letter-spacing: 1px;
      }
      
      .amount-selector {
        position: relative;
        z-index: 101; /* Increased z-index to be above section */
        
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border: 1px solid rgba(54, 202, 186, 0.5);
          border-radius: 10px;
          pointer-events: none;
          z-index: 2;
        }
        
        /* Ensure dropdown appears above other elements */
        .dropdown-menu {
          z-index: 1000;
        }
        
        /* Target the combobox component */
        div[role="combobox"] {
          position: static;
        }
      }
      
      .title-display {
        margin: 15px 0;
        min-height: 50px;
        
        .title-badge {
          display: inline-block;
          font-family: "TheBoldFont", sans-serif;
          font-size: 1.3rem;
          margin: 10px auto;
          padding: 8px 20px;
          border-radius: 20px;
          position: relative;
          transition: all 0.3s ease;
          transform-style: preserve-3d;
          animation: badgeAppear 0.5s forwards;
          box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
          text-align: center;
          
          &::before {
            content: '';
            position: absolute;
            top: -2px;
            left: -2px;
            right: -2px;
            bottom: -2px;
            border-radius: 22px;
            background: linear-gradient(45deg, #fff, transparent, #fff);
            opacity: 0.3;
            z-index: -1;
            animation: borderGlow 2s infinite alternate;
          }
          
          &.npc {
            background: linear-gradient(135deg, #71797E, #43464B); /* IRON */
            color: #e5e7eb;
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
          }
          
          &.squire {
            background: linear-gradient(135deg, #CD7F32, #8B4513); /* BRONZE */
            color: #fff6e6;
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
          }
          
          &.knight {
            background: linear-gradient(135deg, #C0C0C0, #A9A9A9); /* SILVER */
            color: #ffffff;
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
          }
          
          &.duke {
            background: linear-gradient(135deg, #FFD700, #DAA520); /* GOLD */
            color: #fff8e1;
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
          }
          
          &.lord {
            background: linear-gradient(135deg, #236ba4, #1f669f); /* PLATINUM */
            color: #ffffff;
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
          }
          
          &.king {
            background: linear-gradient(135deg, #B9F2FF, #9ACEEB); /* DIAMOND */
            color: #ffffff;
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
            animation: kingGlow 2s infinite alternate;
          }
          
          &.empty {
            background: linear-gradient(135deg, #4b5563, #1f2937);
            color: #9ca3af;
            font-size: 1rem;
          }
          
          &:hover {
            transform: translateY(-3px) rotateX(5deg);
            box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
          }
        }
      }
      
      @keyframes badgeAppear {
        0% {
          opacity: 0;
          transform: scale(0.8) translateY(10px);
        }
        100% {
          opacity: 1;
          transform: scale(1) translateY(0);
        }
      }
      
      @keyframes borderGlow {
        0% {
          opacity: 0.2;
        }
        100% {
          opacity: 0.6;
        }
      }
      
      /* kingGlow animation moved to consolidated section */
    }
    
    /* Action Button */
    .action-button {
      grid-column: 1 / span 2;
      grid-row: 2;
      position: relative;
      height: 60px;
      border-radius: 30px;
      cursor: pointer;
      overflow: hidden;
      transition: all 0.3s ease;
      z-index: 90; /* Lower than amount selection but higher than panel */
      
      .button-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(135deg, #36caba, #1a5e54);
        opacity: 0.9;
        transition: all 0.3s ease;
      }
      
      .button-particles {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: 
          radial-gradient(circle at 20% 20%, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 2px),
          radial-gradient(circle at 80% 30%, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 2px),
          radial-gradient(circle at 40% 70%, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 2px),
          radial-gradient(circle at 60% 80%, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 2px),
          radial-gradient(circle at 70% 15%, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 2px);
        opacity: 0.5;
        animation: particleFloat 3s infinite alternate;
      }
      
      .button-text {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-family: "TheBoldFont", sans-serif;
        font-size: 1.3rem;
        color: white;
        text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
        letter-spacing: 2px;
        white-space: nowrap;
        z-index: 2;
      }
      
      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
        
        .button-bg {
          opacity: 1;
          background: linear-gradient(135deg, #40e0d0, #20756a);
        }
      }
      
      &.pressed {
        transform: translateY(2px);
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
        
        .button-bg {
          opacity: 0.8;
        }
      }
    }
  }
  
