      justify-content: center;
      z-index: 3;
      
      .chest-image {
        width: 100%;
        height: auto;
        transition: transform 0.5s ease;
        margin-top: 50px; /* Reduced from 80px to fit in smaller container */
      }
      
      /* Closed chest container with idle animation */
      .closed-chest-container {
        position: relative;
        width: 100%;
        transition: transform 0.3s ease;
        will-change: transform;
        
        &:hover {
          filter: brightness(1.2);
        }
        
        .chest-idle-glow {
          position: absolute;
          top: 80px; /* Adjusted to match the chest image position */
          left: 15%; /* Added left offset to center the glow */
          width: 70%; /* Reduced from 100% */
          height: calc(100% - 100px); /* Further reduced height */
          background: radial-gradient(ellipse at center, rgba(0, 150, 255, 0.5) 0%, rgba(0, 150, 255, 0.15) 30%, transparent 60%);
          z-index: 2;
          pointer-events: none;
          mix-blend-mode: screen;
          transition: opacity 0.2s ease, filter 0.2s ease;
        }
        

        
        /* Click hint tooltip */
        .chest-click-hint {
          position: absolute;
          top: -40px;
          left: 50%;
          transform: translateX(-50%);
          background: rgba(0, 0, 0, 0.7);
          color: #fff;
          padding: 8px 12px;
          border-radius: 20px;
          font-size: 14px;
          white-space: nowrap;
          opacity: 0;
          animation: fadeInUp 0.3s forwards;
          box-shadow: 0 0 15px rgba(0, 150, 255, 0.5);
          border: 1px solid rgba(0, 150, 255, 0.3);
          
          &::after {
            content: '';
            position: absolute;
            bottom: -8px;
            left: 50%;
            transform: translateX(-50%);
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-top: 8px solid rgba(0, 0, 0, 0.7);
          }
        }
      }
      
      /* Opened chest container with light animation */
      .opened-chest-container {
        position: relative;
        width: 100%;
        
        /* CSS-based light animation */
        .chest-light-animation {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          z-index: 2;
          overflow: hidden;
          
          /* Main light glow */
          .light-glow {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -60%);
