.hero {
  position: relative;
  margin-bottom: 5rem;
  padding: 2rem 0 1rem;
  background: linear-gradient(135deg, #1e1e2f 0%, #2d2d44 100%);
  border-radius: 0 0 30px 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  
  @media (max-width: 768px) {
    padding: 1rem 0 0.5rem;
    margin-bottom: 3rem;
  }
  
  @media (max-width: 576px) {
    padding: 0.5rem 0 0.5rem;
    margin-bottom: 2rem;
  }
  
  // Add animated background elements
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: 
      radial-gradient(circle at 20% 30%, rgba(54, 202, 186, 0.15) 0%, transparent 40%),
      radial-gradient(circle at 80% 70%, rgba(54, 202, 186, 0.1) 0%, transparent 40%);
    z-index: 0;
  }
  
  // Add animated particles
  .animated-particles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 0;
    
    &::before, &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
    }
    
    &::before {
      background-image: 
        radial-gradient(circle at 25% 25%, rgba(54, 202, 186, 0.3) 1px, transparent 1px),
        radial-gradient(circle at 75% 75%, rgba(54, 202, 186, 0.3) 1px, transparent 1px);
      background-size: 40px 40px;
      animation: float 10s ease-in-out infinite alternate;
    }
    
    &::after {
      background-image: 
        radial-gradient(circle at 50% 50%, rgba(255, 255, 255, 0.2) 1px, transparent 1px),
        radial-gradient(circle at 70% 30%, rgba(255, 255, 255, 0.2) 1px, transparent 1px);
      background-size: 30px 30px;
      animation: float 15s ease-in-out infinite alternate-reverse;
    }
  }
  
  // Add animated particles effect
  &::after {
    content: '';
    position: absolute;
    width: 200%;
    height: 200%;
    top: -50%;
    left: -50%;
    background-image: 
      radial-gradient(circle at center, rgba(54, 202, 186, 0.2) 0%, transparent 1px),
      radial-gradient(circle at center, rgba(54, 202, 186, 0.2) 0%, transparent 1px);
    background-size: 30px 30px;
    background-position: 0 0, 15px 15px;
    animation: rotate 120s linear infinite;
    opacity: 0.3;
    z-index: 0;
  }
  
  @media (max-width: 450px) {
    padding-top: 6rem;
  }

  h1 {
    text-transform: uppercase;
    font-size: 6rem;
    text-align: center;
    font-family: "TheBoldFont", sans-serif;
    margin-bottom: 3rem;
    letter-spacing: 2px;
    position: relative;
    z-index: 1;
    animation: float 6s ease-in-out infinite;
    
    // Create a cool text effect with gradient and animation
    background: linear-gradient(90deg, #ffffff, #36caba, #ffffff);
    background-size: 200% auto;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: shimmer 8s linear infinite;
    
    // Add a text shadow for depth
    text-shadow: 
      3px 3px 0px rgba(0, 0, 0, 0.2),
      0 0 10px rgba(54, 202, 186, 0.5);

    span {
      color: #36caba;
      position: relative;
      display: inline-block;
      -webkit-text-fill-color: #36caba;
      animation: glow 3s ease-in-out infinite;
      
      &::after {
        content: '';
        position: absolute;
        bottom: -5px;
        left: 0;
        width: 100%;
        height: 3px;
        background: linear-gradient(90deg, transparent, #36caba, transparent);
        animation: shimmer 4s linear infinite;
      }
    }

    @media (max-width: 991px) {
      font-size: 4rem;
    }

    @media (max-width: 768px) {
      font-size: 3rem;
      font-family: "TheBoldFont", sans-serif;
    }

    @media (max-width: 450px) {
      font-size: 2rem;
      font-family: "TheBoldFont", sans-serif;
    }
  }

  // Add animated coin images
  img {
    position: absolute;
    z-index: 1;
    width: 200px;
    filter: drop-shadow(0 0 10px rgba(54, 202, 186, 0.3));
    transition: all 0.3s ease;

    @media (max-width: 1200px) {
      width: 150px;
    }

    @media (max-width: 768px) {
      width: 120px;
    }

    @media (max-width: 450px) {
      width: 80px;
    }

    &.coin1 {
      top: 0;
      left: 0;
      animation: float 8s ease-in-out infinite;
      
      &:hover {
        transform: rotate(20deg) scale(1.1);
      }
    }

    &.coin2 {
      bottom: 0;
      right: 0;
      animation: float 7s ease-in-out infinite reverse;
      
      &:hover {
        transform: rotate(-20deg) scale(1.1);
      }
    }
  }
}

// Animations
@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  25% {
    transform: translateY(-15px) rotate(5deg);
  }
  50% {
    transform: translateY(0) rotate(0deg);
  }
  75% {
    transform: translateY(15px) rotate(-5deg);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 0.7;
  }
  50% {
    transform: scale(1.05);
    opacity: 1;
  }
}

@keyframes glow {
  0%, 100% {
    filter: brightness(1) drop-shadow(0 0 5px rgba(0, 150, 255, 0.5));
  }
  50% {
    filter: brightness(1.3) drop-shadow(0 0 15px rgba(0, 150, 255, 0.8));
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes lightBeam {
  0% {
    opacity: 0;
    transform: scale(0.5) translateY(0);
  }
  20% {
    opacity: 0.8;
  }
  100% {
    opacity: 0;
    transform: scale(1.5) translateY(-100px);
  }
}

@keyframes particleRise {
  0% {
    transform: translateY(0) scale(1);
    opacity: 0.8;
  }
  100% {
    transform: translateY(-100px) scale(0);
    opacity: 0;
  }
}

@keyframes orbPulse {
  0%, 100% {
    box-shadow: 0 0 20px 5px rgba(0, 150, 255, 0.6);
  }
  50% {
    box-shadow: 0 0 30px 10px rgba(0, 150, 255, 0.8);
  }
}

@keyframes ringRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes buttonPress {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes buttonGlow {
