/* 45-degree movement animations from each corner */
@keyframes moveStarRightDown {
  0% {
    transform: translate(0, 0) rotate(35deg) scale(var(--star-scale, 1));
  }
  100% {
    transform: translate(var(--move-distance, 150px), var(--move-distance, 150px)) rotate(35deg) scale(var(--star-scale, 1));
  }
}

@keyframes moveStarLeftDown {
  0% {
    transform: translate(0, 0) rotate(35deg) scale(var(--star-scale, 1));
  }
  100% {
    transform: translate(calc(var(--move-distance, 150px) * -1), var(--move-distance, 150px)) rotate(35deg) scale(var(--star-scale, 1));
  }
}

@keyframes moveStarRightUp {
  0% {
    transform: translate(0, 0) rotate(35deg) scale(var(--star-scale, 1));
  }
  100% {
    transform: translate(var(--move-distance, 150px), calc(var(--move-distance, 150px) * -1)) rotate(35deg) scale(var(--star-scale, 1));
  }
}

@keyframes moveStarLeftUp {
  0% {
    transform: translate(0, 0) rotate(35deg) scale(var(--star-scale, 1));
  }
  100% {
    transform: translate(calc(var(--move-distance, 150px) * -1), calc(var(--move-distance, 150px) * -1)) rotate(35deg) scale(var(--star-scale, 1));
  }
}

