table.balance-table {
  margin-bottom: 1.5rem;
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 0.8rem;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: -10px;
    left: 0;
    width: 100%;
    height: px;
    background: linear-gradient(90deg, transparent, #36caba, transparent);
    border-radius: 3px;
  }

  @media (max-width: 991px) {
    margin-bottom: 1rem;
  }

  tbody {
    position: relative;
  }

  tr.balance-row {
    position: relative;
    transition: all 0.3s ease;
    background: rgba(30, 30, 47, 0.5);
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 25px rgba(0, 0, 0, 0.1);
    
    &:hover {
      transform: translateX(5px);
      background: rgba(54, 202, 186, 0.1);
      box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
      
      td.text-danger {
        color: #ffffff;
        text-shadow: 0 0 15px rgba(54, 202, 186, 0.8);
      }
      
      .label-icon {
        transform: scale(1.2) rotate(10deg);
        box-shadow: 0 0 15px rgba(54, 202, 186, 0.6);
      }
    }
    
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 3px;
      height: 100%;
      background: #36caba;
      opacity: 0;
      transition: opacity 0.3s ease;
    }
    
    &:hover::after {
      opacity: 1;
    }
    
    &:not(:last-child) {
      margin-bottom: 0.5rem;
    }
  }

  td {
    padding: 1rem 1.5rem;
    position: relative;
    
    &.text-right {
      font-family: 'TheBoldFont', sans-serif;
      font-weight: bold;
      color: #ffffff;
      text-transform: uppercase;
      letter-spacing: 1px;
      font-size: 1.1rem;
      text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.2);
      position: relative;
      display: flex;
      align-items: center;
      
      .label-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background: #36caba;
        color: white;
        margin-right: 10px;
        font-weight: bold;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
        transition: all 0.3s ease;
        font-size: 0.9rem;
        
        &.win {
          background: linear-gradient(135deg, #36caba, #2a9e8e);
        }
        
        &.loss {
          background: linear-gradient(135deg, #36caba, #2a9e8e);
        }
      }
      
      @media (max-width: 768px) {
        font-size: 0.9rem;
        
        .label-icon {
          width: 25px;
          height: 25px;
          font-size: 0.8rem;
        }
      }
    }
    
    &.text-danger {
      font-family: 'TheBoldFont', sans-serif;
      color: #36caba;
      font-weight: bold;
      font-size: 1.8rem;
      text-shadow: 0 0 10px rgba(54, 202, 186, 0.5);
      position: relative;
      transition: all 0.3s ease;
      animation: fadeInUp 0.5s ease-out forwards;
      text-align: right;
      padding-right: 2.5rem;
      
      @keyframes fadeInUp {
        from {
          opacity: 0;
          transform: translateY(10px);
        }
        to {
          opacity: 1;
          transform: translateY(0);
        }
      }
      
      &::after {
        content: '$S';
        position: absolute;
        right: 1rem;
        top: 50%;
        transform: translateY(-50%);
        font-size: 0.7rem;
        color: rgba(255, 255, 255, 0.6);
        text-shadow: none;
      }
      
      @media (max-width: 768px) {
        font-size: 1.4rem;
      }
    }
  }
}

